import React, { Component } from 'react';
import Member from './Member';
import { MEMBERS } from '../members';
import { Typography, Paper } from '@material-ui/core';
import '../styles/AboutUs.css';
class AboutUs extends Component {
  createMemberOverview = () => {
    let view = [];
    for (let member in MEMBERS) {
      view.push(<Member member={MEMBERS[member]} />);
    }
    return view;
  };
  render() {
    return (
      <div className="AboutUS">
        <Typography variant="display2" component="h1">
          Wer sind wir
        </Typography>
        {/* <Paper className="AboutUsEntry"> */}
        <Typography variant="display1" component="h2" className="foo">
          Der Name
        </Typography>
        <Typography variant="body1" component="h3" className="foo">
          Im "Rücken" von Nuglar erhebt sich das Gempen-Plateau mit dem
          Herrenberg. Aus diesem Herrenberg beziehen wir eines der Grundelemente
          für unser Bier: Das Wasser!
        </Typography>
        {/* </Paper> */}

        {/* <Paper className="AboutUsEntry"> */}
        <Typography variant="display1" component="h2" className="foo">
          Vorstand
        </Typography>
        <div className="MemberOverview">{this.createMemberOverview()}</div>
        {/* </Paper>
        <Paper className="AboutUsEntry"> */}
        <Typography variant="display1" component="h2" className="foo">
          Unser Zweck
        </Typography>
        <Typography variant="body1" component="h3" className="foo">
          Betrieb einer Brauerei zur Herstellung von Bieren in gemeinsamer
          Selbsthilfe.
        </Typography>
        <Typography variant="body1" component="h3" className="foo">
          Genossenschafter profitieren von günstigen Konditionen der Produkte.
        </Typography>
        <Typography variant="body1" component="h3" className="foo">
          Zur Zeit sind wir eine "Mikro"-Brauerei mit ca. 2000 Liter
          Braugut/Jahr.
        </Typography>
        {/* </Paper>
        <Paper className="AboutUsEntry"> */}
        <Typography variant="display1" component="h2" className="foo">
          Die Entstehung
        </Typography>
        <Typography variant="body1" component="h3" className="foo">
          Einige Unentwegte Biergeniesser haben sich vor einigen Jahren
          zusammengerauft und im Jahr 2008 die Genossenschaft Brauerei
          Herrenberg gegründet. Zunächst wurde in Garagen gebraut bis im Jahr
          2010 mit der Gemeinde Nuglar-St.Pantaleon ein Mietvertrag für das
          Milchhüsli eingegangen werden konnte. Das Brauen musste im Jahr 2011
          beinah vollständig eingestellt werden, denn der Umbau des Milchhüsli
          hatte Vorrang. Nach 10-monatiger Bautätigkeit konnte im April 2012 die
          Brautätigkeit im neuen Lokal aufgenommen werden.
        </Typography>
        {/* </Paper> */}
        {/* <PaperSheet /> */}
      </div>
    );
  }
}

export default AboutUs;



